<template>
  <div class="song-list">
    <div class="title-row">
      <h4 class="title">榆林小曲曲目欣赏</h4>
    </div>
    <div class="list-container">
      <div class="list-row">
        <div v-for="item in listData" :key="item.id" class="list-item">
          <SongCard
            :url="item.url"
            :name="item.name"
            @click="handlePlayer(item.s_name)"
          />
        </div>
      </div>
      <div class="back-row">
        <img class="back-btn" :src="backImg" alt="" @click="handleBack" />
      </div>
    </div>
  </div>
</template>

<script>
import { SongCard } from "@/components";

export default {
  components: {
    SongCard
  },
  data() {
    return {
      songTitle: require("@/assets/songList/song_title.png"),
      backImg: require("@/assets/songList/back_btn@2x.png"),
      listData: [
        { id: 0, name: "榆林小曲", url: require("@/assets/songList/06_ylxq.png"),s_name:'06_ylxq' },
        { id: 1, name: "啊！我的榆林小曲 ", url: require("@/assets/songList/06_awdylxq.png"),s_name:'06_awdylxq' },
        { id: 2, name: "送郎君", url: require("@/assets/songList/06_slj.png"),s_name:'06_slj' },
        { id: 3, name: "小放牛", url: require("@/assets/songList/06_xfn.png"),s_name:'06_xfn' },
        { id: 4, name: "榆林小曲欣赏", url: require("@/assets/songList/06_ylxqxs.png"),s_name:'06_ylxqxs' },
      ]
    };
  },

  methods: {
    // 返回
    handleBack() {
      this.$router.push("/tune");
    },
    // 播放视频
    handlePlayer(name) {
      this.$router.push({
        path: "/player",
        query: {
          src: name
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../../styles/variables.less";
@import "../../../styles/reset.less";

.song-list {
  width: 100vw;
  height: 100vh;
  background: url("../../../assets/home/tune_bg.png");
  background-size: cover;
  text-align: center;
  position: relative;
  padding: 0 5vw;
  box-sizing: border-box;
  .title-row {
    padding-top: 4vh;
    padding-bottom: 3.6vh;
    text-align: center;
    .title {
      font-family: "systemfont";
      font-size: 42px;
      color: @title;
    }
  }
  .song-title {
    width: 23.3vw;
    display: block;
    padding: 4.8vh 0 3.6vh;
    margin: 0 auto;
  }
  .list-container {
    width: 90vw;
    height: 80vh;
    background: url("../../../assets/songList/song_bg.png") no-repeat;
    background-size: 100% 100%;
    box-sizing: border-box;
    padding: 10vh 4.8vw 6.8vh;
    .list-row {
      display: flex;
      flex-wrap: wrap;
      padding-bottom: 6.8vh;
      .list-item {
        padding-right: 4vw;
        padding-bottom: 4.8vh;
        &:nth-child(4n) {
          padding-right: 0;
        }
        &:nth-child(n+4) {
          padding-bottom: 0;
        }
      }
    }
    .back-row {
      text-align: center;
      .back-btn {
        width: 7vw;
      }
    }
  }
}
</style>
